<template>
  <div id="addEquipment">
    <!-- 智能门锁 -->
    <div class="p10">
      <div class="top_title">
        <div class="title_left">
          <div class="c_title"></div>
          <div>智能门锁</div>
        </div>
        <div @click="showFloor = !showFloor">
          <span :style="{opacity : showFloor == false ? '0.3' : ''}">更换门锁</span>
          <img v-if="showFloor"
               class="chooseIcon"
               src="@/assets/btn_selected.png"
               alt="">
          <img v-else
               class="chooseIcon"
               style="opacity:0.2;"
               src="@/assets/btn_normal.png"
               alt="">
        </div>
      </div>
      <div v-if="showFloor == true">
        <div v-for="(item,i) in allRooms"
             :key="i"
             class="room">
          <div style="width:20%;text-align:left;">{{ item.roomSn }}</div>
          <div style="width:24%;"
               @click="showAll(i,'lock')"
               :style="{color:item.hotelLockType || item.lockType ?'':'#888'}">
            {{ lockInstallTypeObj[item.lockType] || lockInstallTypeObj[item.hotelLockType] || item.lockType_title }}
            <img class="rightIcon"
                 src="@/assets/Chevron.png"
                 alt="">
          </div>
          <div style="width:20%;"
               @click="showAll(i,'order')"
               :style="{color:item.lockInstall || item.installType?'':'#888'}">
            {{ lockTypeObj[item.installType] || lockTypeObj[item.lockInstall] || item.installType_title }}
            <img class="rightIcon"
                 src="@/assets/Chevron.png"
                 alt="">
          </div>
          <!-- {{ item.doorType_title || doorTypesAll.find(itm=>itm.dictValue == item.doorType).dictLabel }} -->
          <!-- {{ item.doorType ? doorTypesAll.find(itm=>itm.dictValue == item.doorType).dictLabel : item.doorType_title}} -->
          <div v-if="doorTypesAll.length > 0"
               style="width:24%;"
               @click="showAll(i,'open')"
               :style="{color:item.doorType?'':'#888'}">
            {{ doorTypeObj[item.doorType] || item.doorType_title }}
            <img class="rightIcon"
                 src="@/assets/Chevron.png"
                 alt="">
          </div>
        </div>
        <div class="addroomIcon"
             @click="openShowRooms">添加房间</div>
      </div>
    </div>
    <!-- 智慧设备 -->
    <div class="p10">
      <div class="top_title">
        <div class="title_left">
          <div class="c_title"
               style="background:#FF7A41;"></div>
          <div>智慧设备</div>
        </div>
      </div>
      <!-- <van-cell-group v-for="(item,i) in devInfo"
                      :key="i">
        <div class="stepper_flex">
          <van-cell title="自助机"
                    v-model="item.devName"
                    is-link
                    :label="'¥'+item.price+'/套'"
                    @click="showMachineType = true" />
          <van-stepper v-if="item.devName != '无'"
                       v-model="item.num" />
        </div>
      </van-cell-group> -->
      <van-cell-group>
        <div class="stepper_flex">
          <van-cell title="自助机"
                    v-model="machineType"
                    is-link
                    :label="'¥'+machinePrice+'/套'"
                    @click="showMachineType = true" />
          <van-stepper v-if="machineType != '无'"
                       v-model="machineStepper" />
        </div>
        <div class="stepper_flex">
          <van-cell title="空调电控"
                    v-model="airEleType"
                    is-link
                    :label="'¥'+airElePrice+'/套'"
                    @click="showAirEle = true" />
          <van-stepper v-if="airEleType != '无'"
                       v-model="airEleStepper" />
        </div>
        <div class="stepper_flex">
          <van-cell title="智能网关"
                    v-model="gateWayType"
                    is-link
                    :label="'¥'+gateWayPrice+'/套'"
                    @click="showGateWay = true" />
          <van-stepper v-if="gateWayType != '无'"
                       v-model="gateWayStepper" />
        </div>
      </van-cell-group>
    </div>
    <!-- 物料 -->
    <div class="p10">
      <div class="top_title">
        <div class="title_left">
          <div class="c_title"
               style="background:#05B96C;"></div>
          <div>物料</div>
        </div>
      </div>
      <div style="margin-bottom: 50px;">
        <div v-for="(item,i) in materielTypes"
             :key="i">
          <div style="border-bottom:1px solid #eee;">
            <div class="m_box">
              <div style="text-align:left;width:70%;">
                <div>{{ item.dictLabel || item.devName }}</div>
                <div class="c_98">¥{{ item.extraValue || item.price }}/套</div>
              </div>
              <div>
                <van-stepper v-model="item.num"
                             min='0' />
              </div>
              <!-- <img v-if="item.dictValue == 'HLB'"
                   @click="isLight = !isLight"
                   class="chooseIcon"
                   :style="{opacity:isLight == false?'0.2':''}"
                   :src="isLight == true?require('@/assets/btn_selected.png'):require('@/assets/btn_normal.png')">
              <img v-if="item.dictValue == 'QR'"
                   @click="isCode = !isCode"
                   class="chooseIcon"
                   :style="{opacity:isCode == false?'0.2':''}"
                   :src="isCode == true?require('@/assets/btn_selected.png'):require('@/assets/btn_normal.png')"> -->
              <!-- <van-stepper v-if="item.dictValue == 'XLQR' || item.devValue == 'XLQR'"
                           v-model="stepper" /> -->
            </div>
            <div v-if="item.dictValue == 'QR' || item.devValue== 'QR'"
                 class="code_box">
              <div v-for="(item,i) in codeSuit"
                   :key="i"
                   class="code_type">
                <div style="width:80%;">{{ item.dictLabel }}</div>
                <div>*{{ item.extraValue > '0' ? item.extraValue : allRooms.length }}</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- 酒店信息 -->
    <!-- <div class="p10">
      <div class="top_title">
        <div class="title_left">
          <div></div>
          <div>酒店信息</div>
        </div>
      </div>
      <div>
        <van-field v-model="value"
                   label="美团hos指数"
                   placeholder="请输入美团hos指数" />
        <van-cell title="装修时间"
                  v-model="time"
                  is-link
                  @click="showTime = true" />
      </div>
    </div> -->
    <div class="flex bottom_box">
      <div class="bottom_l"
           @click="goNext('/edit-or-see-apply','keep')">保存信息</div>
      <div class="bottom_l bottom_r"
           @click="goNext('/edit-or-see-apply','edit')">下一步</div>
    </div>
    <!-- 智能锁类型 -->
    <van-popup v-model="showLockType"
               round
               position="bottom">
      <van-picker title="智能锁类型"
                  show-toolbar
                  :columns="lockTypes"
                  @confirm="onConfirmLockType"
                  @cancel="showLockType = false" />
    </van-popup>
    <!-- 装锁方式 -->
    <van-popup v-model="showDoorInstall"
               round
               position="bottom">
      <van-picker title="装锁方式"
                  show-toolbar
                  :columns="doorInstalls"
                  @confirm="onConfirmDoorInstalls"
                  @cancel="showDoorInstall = false" />
    </van-popup>
    <!-- 开门类型 -->
    <van-popup v-model="showDoorType"
               round
               position="bottom">
      <van-picker title="开门类型"
                  show-toolbar
                  :columns="doorTypes"
                  @confirm="onConfirmDoorTypes"
                  @cancel="showDoorType = false" />
    </van-popup>
    <!-- 自助机 -->
    <van-popup v-model="showMachineType"
               round
               position="bottom">
      <van-picker title="自助机"
                  show-toolbar
                  :columns="autoMachine"
                  @confirm="onConfirmMachineType"
                  @cancel="showMachineType = false" />
    </van-popup>
    <!-- 空调电控 -->
    <van-popup v-model="showAirEle"
               round
               position="bottom">
      <van-picker title="空调电控"
                  show-toolbar
                  :columns="hotelElecDev"
                  @confirm="onConfirmAirEle"
                  @cancel="showAirEle = false" />
    </van-popup>
    <!-- 智能网关 -->
    <van-popup v-model="showGateWay"
               round
               position="bottom">
      <van-picker title="智能网关"
                  show-toolbar
                  :columns="hotelGateWay"
                  @confirm="onConfirmGateWay"
                  @cancel="showGateWay = false" />
    </van-popup>
    <!-- 时间 -->
    <van-popup v-model="showTime"
               round
               position="bottom">
      <van-picker title="时间"
                  show-toolbar
                  :columns="times"
                  @confirm="onConfirmTime"
                  @cancel="showTime = false" />
    </van-popup>
    <!-- 添加房间 -->
    <van-popup v-model="showRooms"
               position="bottom"
               style="line-height: 50px;">
      <div class="flex"
           v-if="addAllRooms.length > 0"
           style="border-bottom:4px solid #F6F3F6;">
        <div class="flex"
             style="padding-left: 120px;">
          <div>选择房间号</div>
          <div class="flex"
               @click="chooseAllRooms">
            <div style="padding:0 10px 0 60px;">全选</div>
            <img v-if="allRoomsStatus == true"
                 class="chooseIcon"
                 src="@/assets/btn_selected.png"
                 alt="">
            <img v-else
                 class="chooseIcon"
                 style="opacity:0.2;"
                 src="@/assets/btn_normal.png"
                 alt="">
          </div>
        </div>
      </div>
      <div class="room_box_scroll"
           v-if="addAllRooms.length > 0">
        <div v-for="(item,i) in addAllRooms"
             :key="i"
             @click="chooseRoom(item)"
             class="room_info">
          <div style="padding-right: 20px;">{{ item.roomSn }}</div>
          <img v-if="item.status == true"
               class="chooseIcon"
               src="@/assets/btn_selected.png"
               alt="">
          <img v-else
               class="chooseIcon"
               style="opacity:0.2;"
               src="@/assets/btn_normal.png"
               alt="">
        </div>
      </div>
      <div v-else
           style="color:red;line-height:80px;">暂无房间,请帮助酒店新建房间号</div>
      <div class="flex"
           style="border-top:4px solid #F6F3F6;">
        <div class="border_eee"
             :style="{width:addAllRooms.length > 0?'50%':'100%'}"
             @click="showRooms = false">{{ addAllRooms.length > 0?'取消':'确定' }}</div>
        <div class="border_eee"
             style="color:#f60;"
             v-if="addAllRooms.length > 0"
             @click="choosedRooms">确定</div>
      </div>
    </van-popup>
  </div>
</template>
<script>
import { GetSystemDictDataByType } from '@/api/api_query';
import { GetBusinessRoomUninstallApplyByhotel, PostBusinessHotelLockInstallAdd, GetBusinessHotelLockInstallByaudit, PostBusinessHotelLockInstallEdit } from '@/api/api_equip';
import { Toast } from 'vant';
import dayjs from 'dayjs';
export default {
    data() {
        return {
            allRooms: [],
            value: null,
            aaa: null,
            my_hotelLockType: null,
            my_lockInstall: null,
            my_doorType: null,
            lockInstallTypeObj: {},
            lockTypeObj: {},
            doorTypeObj: {},
            machineType: '请选择型号',
            airEleType: '请选择型号',
            gateWayType: '请选择型号',
            showFloor: false,
            showLockType: false,
            showDoorInstall: false,
            showDoorType: false,
            showRooms: false,
            allRoomsStatus: false,
            showMachineType: false,
            showAirEle: false,
            showGateWay: false,
            isLight: false,
            isCode: false,
            showTime: false,
            nothingID: false,
            time: null,
            times: [],
            lockTypes: [],
            doorInstalls: [],
            doorTypes: [],
            autoMachine: [],
            hotelElecDev: [],
            hotelGateWay: [],
            machineStepper: 0,
            airEleStepper: 0,
            gateWayStepper: 0,
            stepper: 1,
            addAllRooms: [],
            materielTypes: [],
            codeSuit: [],
            machinePrice: 0,
            airElePrice: 0,
            gateWayPrice: 0,
            isEdit: '0'
        };
    },
    created() {
        this.$store.commit('setHeader', '添加设备');
        this.getType();
        this.getLockInfo();
    },
    methods: {
        // 根据流程查询门锁工单信息
        getLockInfo() {
            GetBusinessHotelLockInstallByaudit({ auditApprovalId: this.$route.query.auditApprovalId }).done((res) => {
                if (res.data) {
                    this.$store.commit('setHeader', '编辑设备');
                    this.isEdit = '1';
                    if (res.data.lockInfo) {
                        this.showFloor = true;
                    }
                    this.id = res.data.id;
                    this.allRooms = res.data.lockInfo ? JSON.parse(res.data.lockInfo) : [];
                    this.devInfo = res.data.devInfo ? JSON.parse(res.data.devInfo) : [];
                    this.materielTypes = res.data.materialInfo ? JSON.parse(res.data.materialInfo) : [];
                    // 自助机
                    let info_machine = this.devInfo.find((item) => item.type == 'machine_type');
                    this.machineType = info_machine ? info_machine.devName : '无';
                    this.machinePrice = info_machine ? info_machine.price : '0';
                    this.machineValue = info_machine ? info_machine.devValue : null;
                    this.machineStepper = info_machine ? info_machine.num : '0';
                    // 空调电控
                    let info_airEle = this.devInfo.find((item) => item.type == 'airEle_type');
                    this.airEleType = info_airEle ? info_airEle.devName : '无';
                    this.airElePrice = info_airEle ? info_airEle.price : '0';
                    this.airEleValue = info_airEle ? info_airEle.devValue : null;
                    this.airEleStepper = info_airEle ? info_airEle.num : '0';
                    // 智能网关
                    let info_gateWay = this.devInfo.find((item) => item.type == 'gateWay_type');
                    this.gateWayType = info_gateWay ? info_gateWay.devName : '无';
                    this.gateWayPrice = info_gateWay ? info_gateWay.price : '0';
                    this.gateWayValue = info_gateWay ? info_gateWay.devValue : null;
                    this.gateWayStepper = info_gateWay ? info_gateWay.num : '0';
                    this.stepper = this.materielTypes.find((item) => item.devValue == 'XLQR').num;
                    // 二维码标识套装
                    GetSystemDictDataByType({ dictType: 'business_hotel_qr_suit' }).done((res) => {
                        this.codeSuit = res.data;
                    });
                }
            });
        },
        // 根据酒店查询未申请装锁房间
        getRooms() {
            GetBusinessRoomUninstallApplyByhotel({ hotelId: this.$route.query.applyHotelId }).done((res) => {
                this.addAllRooms = res.data.map((item) => {
                    item.lockType_title = '锁类型';
                    item.installType_title = '装锁方式';
                    item.doorType_title = '开门类型';
                    if (item.hotelLockType) {
                        item.price = this.lockTypesAll.find((itm) => itm.dictValue == item.hotelLockType).extraValue;
                    } else {
                        item.price = '0';
                    }
                    item.status = false;
                    return item;
                });
            });
        },
        getType() {
            // 智能锁类型
            GetSystemDictDataByType({ dictType: 'business_lock_install_type' }).done((res) => {
                this.lockTypesAll = res.data;
                let obj = {};
                res.data.map((item) => {
                    obj[item.dictValue] = item.dictLabel;
                });
                this.lockInstallTypeObj = obj;
                this.lockTypes = res.data.map((item) => {
                    return item.dictLabel;
                });
            });
            // 装锁方式
            GetSystemDictDataByType({ dictType: 'business_room_door_install' }).done((res) => {
                this.doorInstallsAll = res.data;
                let obj = {};
                res.data.map((item) => {
                    obj[item.dictValue] = item.dictLabel;
                });
                this.lockTypeObj = obj;
                this.doorInstalls = res.data.map((item) => {
                    return item.dictLabel;
                });
            });
            // 开门类型
            GetSystemDictDataByType({ dictType: 'business_room_door_type' }).done((res) => {
                this.doorTypesAll = res.data;
                let obj = {};
                res.data.map((item) => {
                    obj[item.dictValue] = item.dictLabel;
                });
                this.doorTypeObj = obj;
                this.doorTypes = res.data.map((item) => {
                    return item.dictLabel;
                });
            });
            // 自助机
            GetSystemDictDataByType({ dictType: 'business_auto_machine' }).done((res) => {
                let obj = {
                    dictLabel: '无',
                    dictValue: 'noMachine',
                    extraValue: '0',
                    type: 'autoMachine'
                };
                res.data.push(obj);
                this.autoMachineAll = res.data;
                this.autoMachine = res.data.map((item) => {
                    return item.dictLabel;
                });
                this.machineType = this.autoMachineAll[0].dictLabel;
                this.machinePrice = this.autoMachineAll[0].extraValue;
                this.machineValue = this.autoMachineAll[0].dictValue;
            });
            // 空调电控
            GetSystemDictDataByType({ dictType: 'business_hotel_elec_dev' }).done((res) => {
                let obj = {
                    dictLabel: '无',
                    dictValue: 'noElec',
                    extraValue: '0',
                    type: 'elecDev'
                };
                res.data.push(obj);
                this.hotelElecDevAll = res.data;
                this.hotelElecDev = res.data.map((item) => {
                    return item.dictLabel;
                });
                this.airEleType = this.hotelElecDevAll[0].dictLabel;
                this.airElePrice = this.hotelElecDevAll[0].extraValue || '0';
                this.airEleValue = this.hotelElecDevAll[0].dictValue;
            });
            // 智能网关
            GetSystemDictDataByType({ dictType: 'business_gate_way' }).done((res) => {
                let obj = {
                    dictLabel: '无',
                    dictValue: 'noElec',
                    extraValue: '0',
                    type: 'gateWay'
                };
                res.data.push(obj);
                this.hotelGateWayAll = res.data;
                this.hotelGateWay = res.data.map((item) => {
                    return item.dictLabel;
                });
                this.gateWayType = this.hotelGateWayAll[0].dictLabel;
                this.gateWayPrice = this.hotelGateWayAll[0].extraValue || '0';
                this.gateWayValue = this.hotelGateWayAll[0].dictValue;
            });
            // 物料类型
            GetSystemDictDataByType({ dictType: 'business_hotel_material' }).done((res) => {
                this.materielTypes = res.data;
            });
            // 二维码标识套装
            GetSystemDictDataByType({ dictType: 'business_hotel_qr_suit' }).done((res) => {
                this.codeSuit = res.data;
                this.getRooms();
            });
            this.time = dayjs().year();
            let thisYear = dayjs().year();
            for (let i = 0; i <= 5; i++) {
                this.times.push(thisYear--);
            }
        },
        // 房间-选择类型
        showAll(i, type) {
            this.index = i;
            if (type == 'lock') {
                this.showLockType = true;
            } else if (type == 'order') {
                this.showDoorInstall = true;
            } else if (type == 'open') {
                this.showDoorType = true;
            }
        },
        // 房间-锁类型
        onConfirmLockType(value) {
            let current = this.lockTypesAll.find((item) => {
                if (item.dictLabel == value) {
                    return item;
                }
            });
            this.allRooms.map((item, i) => {
                if (this.index == i) {
                    item.lockType_title = value;
                    item.price = current.extraValue;
                    item.hotelLockType = current.dictValue;
                    item.lockType = current.dictValue;
                }
            });
            this.showLockType = false;
        },
        // 房间-装锁方式
        onConfirmDoorInstalls(value) {
            let current = this.doorInstallsAll.find((item) => {
                if (item.dictLabel == value) {
                    return item;
                }
            });
            this.allRooms.map((item, i) => {
                if (this.index == i) {
                    item.installType_title = value;
                    item.lockInstall = current.dictValue;
                    item.installType = current.dictValue;
                }
            });
            this.showDoorInstall = false;
        },
        // 房间-开门类型
        onConfirmDoorTypes(value) {
            let current = this.doorTypesAll.find((item) => {
                if (item.dictLabel == value) {
                    return item;
                }
            });
            this.allRooms.map((item, i) => {
                if (this.index == i) {
                    item.doorType_title = value;
                    item.doorType = current.dictValue;
                    item.doorType = current.dictValue;
                }
            });
            this.showDoorType = false;
        },
        // 设备-自助机
        onConfirmMachineType(value) {
            this.machineType = value;
            let c_machine = this.autoMachineAll.find((item) => {
                if (item.dictLabel == value) {
                    return item;
                }
            });
            this.machinePrice = c_machine.extraValue;
            this.machineValue = c_machine.dictValue;
            this.showMachineType = false;
        },
        // 设备-空调电控
        onConfirmAirEle(value) {
            this.airEleType = value;
            let c_airEle = this.hotelElecDevAll.find((item) => {
                if (item.dictLabel == value) {
                    return item;
                }
            });
            this.airElePrice = c_airEle.extraValue;
            this.airEleValue = c_airEle.dictValue;
            this.showAirEle = false;
        },
        // 设备-智能网关
        onConfirmGateWay(value) {
            this.gateWayType = value;
            let c_gateWay = this.hotelGateWayAll.find((item) => {
                if (item.dictLabel == value) {
                    return item;
                }
            });
            this.gateWayPrice = c_gateWay.extraValue;
            this.gateWayValue = c_gateWay.dictValue;
            this.showGateWay = false;
        },
        // 选择时间
        onConfirmTime(value) {
            this.time = value;
            this.showTime = false;
        },
        //
        onConfirm() {},
        // 添加房间
        openShowRooms() {
            this.showRooms = true;
            this.allRoomsStatus = false;
            if (this.allRooms.length > 0) {
                this.choosedIds = this.allRooms.map((item) => item.roomId || item.id);
                if (this.id && this.nothingID == false) {
                    this.addAllRooms = this.addAllRooms.filter((item) => !this.choosedIds.includes(item.id));
                    this.nothingID = true;
                } else {
                    this.addAllRooms = this.addAllRooms.filter((item) => !this.choosedIds.includes(item.id));
                }
            }
        },
        // 全选-房间
        chooseAllRooms() {
            this.addAllRooms.map((item) => {
                item.status = this.allRoomsStatus == true ? false : true;
            });
            this.allRoomsStatus = !this.allRoomsStatus;
        },
        // 选择-房间
        chooseRoom(item) {
            this.addAllRooms.map((itm) => {
                if (itm.id == item.id) {
                    itm.status = !itm.status;
                }
            });
        },
        // 选中-房间
        choosedRooms() {
            let isHave = this.addAllRooms.some((item) => {
                return item.status == true;
            });
            if (isHave == false) {
                Toast('至少选择一个房间号');
            } else {
                let aaa = this.addAllRooms.filter((item) => {
                    if (item.status == true) {
                        item.lockType_title = '锁类型';
                        item.installType_title = '装锁方式';
                        item.doorType_title = '开门类型';
                        return item;
                    }
                });
                this.allRooms = this.allRooms.concat(aaa);
                this.showRooms = false;
            }
        },
        // 下一步
        goNext(url, type) {
            if (this.showFloor == true) {
                let roomList = this.allRooms.filter((item) => {
                    if (item.hotelLockType && item.lockInstall && item.doorType) {
                        return item;
                    } else if (item.lockType && item.installType && item.doorType) {
                        return item;
                    }
                });
                if (roomList.length >= 0 && this.allRooms.length != roomList.length) {
                    Toast('请选择房间信息');
                    return;
                } else {
                    this.lockInfo = [];
                    roomList.map((item) => {
                        let lock_obj = {};
                        lock_obj.roomId = item.id || item.roomId;
                        lock_obj.roomSn = item.roomSn;
                        lock_obj.price = item.price;
                        lock_obj.lockType = item.lockType || item.hotelLockType;
                        lock_obj.installType = item.installType || item.lockInstall;
                        lock_obj.doorType = item.doorType;
                        this.lockInfo.push(lock_obj);
                    });
                    this.totlePrice_lock = this.lockInfo.reduce((totle, item) => {
                        return Number(totle) + Number(item.price);
                    }, 0);
                }
            } else {
                this.totlePrice_lock = 0;
            }
            // 设备
            this.devInfo = [];
            // 自助机
            let dev_obj_machine = {};
            if (this.machineType != '无' && this.machineType != '无') {
                dev_obj_machine.devName = this.machineType;
                dev_obj_machine.devValue = this.machineValue;
                dev_obj_machine.price = this.machinePrice;
                dev_obj_machine.num = this.machineStepper;
                dev_obj_machine.type = 'machine_type';
                this.devInfo.push(dev_obj_machine);
            }
            // 空调电控
            let dev_obj_airEle = {};
            if (this.airEleType != '无' && this.airEleType != '请选择型号') {
                dev_obj_airEle.devName = this.airEleType;
                dev_obj_airEle.devValue = this.airEleValue;
                dev_obj_airEle.price = this.airElePrice;
                dev_obj_airEle.num = this.airEleStepper;
                dev_obj_airEle.type = 'airEle_type';
                this.devInfo.push(dev_obj_airEle);
            }
            // 智能网关
            let dev_obj_gateWay = {};
            if (this.gateWayType != '无' && this.gateWayType != '请选择型号') {
                dev_obj_gateWay.devName = this.gateWayType;
                dev_obj_gateWay.devValue = this.gateWayValue;
                dev_obj_gateWay.price = this.gateWayPrice;
                dev_obj_gateWay.num = this.gateWayStepper;
                dev_obj_gateWay.type = 'gateWay_type';
                this.devInfo.push(dev_obj_gateWay);
            }
            this.totlePrice_airEle = this.devInfo.reduce((totle, item) => {
                return Number(totle) + Number(item.price) * Number(item.num);
            }, 0);
            // 物料
            this.materialInfo = [];
            this.materielTypes.map((item) => {
                let materiel_obj = {};
                materiel_obj.devName = item.dictLabel || item.devName;
                materiel_obj.devValue = item.dictValue || item.devValue;
                materiel_obj.num = item.num;
                materiel_obj.price = item.extraValue || item.price;
                this.materialInfo.push(materiel_obj);
            });
            this.totlePrice_material = this.materialInfo.reduce((totle, item) => {
                return Number(totle) + Number(item.price) * Number(item.num);
            }, 0);
            let data = {
                auditApprovalId: this.$route.query.auditApprovalId,
                hotelId: this.$route.query.applyHotelId,
                totalPrice: this.totlePrice_lock + this.totlePrice_airEle + this.totlePrice_material,
                lockInfo: this.showFloor == false ? '' : JSON.stringify(this.lockInfo),
                devInfo: this.devInfo.length > 0 ? JSON.stringify(this.devInfo) : '',
                materialInfo: JSON.stringify(this.materialInfo)
            };
            if (this.isEdit == '1') {
                delete data.auditApprovalId;
                delete data.hotelId;
                data.id = this.id;
                PostBusinessHotelLockInstallEdit(data).done((res) => {
                    Toast.success(res.msg);
                    if (type == 'edit') {
                        this.$router.push({
                            path: url,
                            query: {
                                type: type,
                                role: this.$route.query.role,
                                applyHotelId: this.$route.query.applyHotelId,
                                applyType: this.$route.query.applyType,
                                hotelId: this.$route.query.hotelId,
                                auditApprovalId: this.$route.query.auditApprovalId
                            }
                        });
                    }
                });
            } else {
                delete data.id;
                PostBusinessHotelLockInstallAdd(data).done((res) => {
                    data.auditApprovalId = this.$route.query.auditApprovalId;
                    data.hotelId = this.$route.query.hotelId;
                    Toast.success(res.msg);
                    if (type == 'edit') {
                        this.$router.push({
                            path: url,
                            query: {
                                type: type,
                                role: this.$route.query.role,
                                applyHotelId: this.$route.query.applyHotelId,
                                applyType: this.$route.query.applyType,
                                hotelId: this.$route.query.hotelId,
                                auditApprovalId: this.$route.query.auditApprovalId
                            }
                        });
                    } else {
                        this.isEdit == '1';
                        this.getType();
                        this.getLockInfo();
                    }
                });
            }
        }
    }
};
</script>
<style scoped>
#addEquipment {
    overflow: auto;
    font-size: 14px;
    color: #000;
    position: fixed;
    top: 50px;
    bottom: 0;
    left: 0;
    right: 0;
}
.top_title {
    border-bottom: 1px solid #eee;
    display: flex;
    justify-content: space-between;
    padding: 10px 14px 10px 4px;
    font-size: 15px;
}
.title_left {
    display: flex;
    align-items: center;
}
.c_title {
    background: #2e7bf7;
    width: 4px;
    display: inline-block;
    height: 14px;
    border-radius: 2px;
    margin-right: 4px;
}
.room {
    display: flex;
    justify-content: space-between;
    padding: 4px 14px;
    line-height: 29px;
    border-bottom: 1px solid #eee;
}
.p10 {
    background: #fff;
    padding: 0px 10px;
    margin: 10px 0;
}
.rightIcon {
    width: 4px;
    height: 8px;
}
.chooseIcon {
    width: 16px;
    height: 16px;
    vertical-align: middle;
}
.addroomIcon {
    color: #2f7bf7;
    line-height: 40px;
}
.room_box_scroll {
    overflow: auto;
    max-height: calc(100vh - 380px);
}
.room_info {
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom: 1px solid #eee;
}
.border_eee {
    border: 1px solid #eee;
    width: 50%;
}
.m_box {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 14px;
}
.code_box {
    display: flex;
    flex-wrap: wrap;
    padding: 2px 16px;
    text-align: left;
    color: #888;
}
.code_type {
    width: 40%;
    margin-bottom: 10px;
    margin-right: 30px;
    display: flex;
    align-items: baseline;
}
.c_98 {
    color: #989598;
}
.flex {
    display: flex;
    justify-content: center;
    align-items: center;
}
.bottom_box {
    justify-content: space-around;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    line-height: 40px;
}
.bottom_l {
    border: 1px solid #eee;
    width: 50%;
    background: #fff;
    color: #000;
}
.bottom_r {
    border: 1px solid #eee;
    width: 50%;
    background: #ff7a41;
    color: #fff;
}
.stepper_flex {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
::v-deep .van-field__label,
::v-deep .van-cell__title {
    text-align: left;
}
::v-deep .van-cell__value {
    color: #000;
}
::v-deep .van-cell {
    line-height: 14px;
    align-items: center;
}
::v-deep .van-cell__right-icon {
    text-align: left;
    color: #000;
}
::v-deep .van-field__label {
    width: 135px;
}
::v-deep .van-cell {
    width: 70%;
    border-bottom: 1px solid #eee;
}
</style>



